@use "../SassStyles/mixin";

$dropdown-fontsize: 1.3rem;
$padding-select-item: 0.3rem;

.custom-select-dropdown {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  min-width: 5vw;
  cursor: default;
  border-radius: 4px;
  text-align: left;

}

.span-current-value {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: $padding-select-item;
  justify-content: center;
  background-color: var(--bg-secondary);
  font-size: $dropdown-fontsize;
  margin: 0;
  font-weight: bold;
}

.dropdown-select-content {
  display: none;
  position: absolute;
  top: calc($dropdown-fontsize + ($padding-select-item * 2) + 0.3rem);
  left: -2px;
  z-index: 1;
  border: 2px solid black;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.dropdown-select-item {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 0.2rem;
  width: 100%;
  height: 100%;
  background-color: var(--bg-secondary);
}

.dropdown-select-item:hover {
  background-color: var(--tertiary);
  color: white;
  font-weight: bold;
}

.show-content {
  min-width: 5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.show-content-up {
  top: -275px;
  left: -2px;
}

.show-content * {
  list-style: none;
  font-size: 1.3rem;
  margin: 0;
}

.arrow-select-dropdown {
  position: absolute;
  right: 0;
  border: solid var(--color);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  margin: 10px;
}

.arrow-select-up {
  transform: rotate(-135deg);
  transition: 300ms;
}

.arrow-select-down {
  transform: rotate(45deg);
  transition: 300ms;
}

// Only for the to dos, doesn't have to do much with the selector itself
.select-color-todo {
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

@media (max-width: 900px) {
  .custom-select-dropdown {
    min-width: 10vw;
  } 
  .show-content {
    min-width: 10vw;
  }
}

@media (max-width: 500px) {
  .custom-select-dropdown {
    min-width: 20vw;
  } 
  .show-content {
    min-width: 20vw;
  }
}