@use "./mixin";
@use "./animations";

.title {
  font-size: 2rem;
  @include mixin.spacing-s;
}

.title-s {
  font-size: 1.5rem;
  margin: 0.3rem;
}

.error-msg {
  @include mixin.alert-text;
  animation: error-shake 250ms ;
  width: 100%;
  word-wrap: break-word;
  text-align: center;
}

.success-msg {
  @include mixin.success-text;
  width: 100%;
  word-wrap: break-word;
  text-align: center;
}